import "./addbookmark.css";
import * as React from "react";
import { Link } from 'react-router-dom'
import { useState } from "react";
import { addActions } from "../store/addbookmarkstore";
import { useDispatch} from "react-redux";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";
import { CardActionArea, CardActions } from "@mui/material";

export default function AddbookMark() {
  //Side Navigation

  const dispatch = useDispatch();

  const [title, setTitle] = useState("");

  const titleHandler = (event) => {
    setTitle(event.target.value); //TITLE
  };

  const [name, setName] = useState("");

  const nameHandler = (event) => {
    setName(event.target.value); //BOOKMARKNAME
  };

  const [url, setUrl] = useState("");

  const urlHandler = (event) => {
    setUrl(event.target.value); //URL
  };

  const AddBookMarkHandler = () => {
    let payload = {
      id: 1,
      title: title,
      name: name,
      url: url,
    };
    dispatch(addActions.AddBookmark(payload));
  };
  
  return (
    <>
      <div class="container">
        <div className="wrapper">
          <img src="../Bookmark.png" alt="Company logo"></img>
        </div>
        <Stack direction="row" spacing={2}>
          <Button variant="contained" href="#text-buttons">
            SIGN OUT
          </Button>
        </Stack>
      </div>

      <div className="blankspace"></div>
      <div className="midcontainer">
        <div className="side-nav">
          <Stack spacing={2}>
            <Link  to='/dashboard' >DASHBOARD</Link>

            <Button>ADD BOOKMARK</Button>

            <Button href='/favourites'>FAVOURITES</Button>
          </Stack>
        </div>

        <div class="item1">
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Add Bookmark
                </Typography>
                <Typography variant="h6" color="text.secondary">
                  Add Title
                  <Box
                    component="form"
                    sx={{
                      "& > :not(style)": { m: 1, width: "25ch" },
                    }}
                    noValidate
                    autoComplete="off"
                  >
                    <TextField
                      id="outlined-basic"
                      label="Outlined"
                      variant="outlined" //TITLE
                      value={title}
                      onChange={titleHandler}
                    />
                  </Box>
                </Typography>
                <Typography variant="h6" color="text.secondary">
                  Bookmark Name
                  <Box
                    component="form"
                    sx={{
                      "& > :not(style)": { m: 1, width: "25ch" },
                    }}
                    noValidate
                    autoComplete="off"
                  >
                    <TextField
                      id="outlined-basic"
                      label="Outlined"
                      variant="outlined" //BOOKMARK NAME
                      value={name}
                      onChange={nameHandler}
                    />
                  </Box>
                </Typography>
                <Typography variant="h6" color="text.secondary">
                  URL
                  <Box
                    component="form"
                    sx={{
                      "& > :not(style)": { m: 1, width: "25ch" },
                    }}
                    noValidate
                    autoComplete="off"
                  >
                    <TextField
                      id="outlined-basic"
                      label="Outlined" //URL
                      variant="outlined"
                      value={url}
                      onChange={urlHandler}
                    />
                  </Box>
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" color="primary" onClick={AddBookMarkHandler}>
                CREATE
              </Button>
            </CardActions>
          </Card>
        </div>
      </div>
    </>
  );
}
